body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input,
textarea {
  margin: 0.5rem;
  width: 90%;
}

button {
  margin-bottom: 1rem;
  padding: 0.4rem 1rem;
  border: 0;
  border-radius: 15px;
  color: white;
  background-color: black;
  cursor: pointer;
}

.Forms {
  float: left;
  width: 40%;
  height: 88vh;
  overflow-y: scroll;
}

.GenInfo,
.Education,
.WorkExp {
  margin: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 10px black;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  height: 100%;
  width: 80%;
}

.Header {
  border-radius: 0px 0px 15px 15px;
  padding: 0.3rem;
  background: black;
  color: white;
  height: 100%;
  box-shadow: 0 0 20px black;
  text-align: center;
}

.CV {
  float: left;
}
